@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{display:flex;align-items:center;justify-content:center;height:100vh;background-color:#167c7e;color:white;font-family:"Roboto", sans-serif;font-size:16px}.main .timer{max-width:430px;padding:1rem}.main .timer button{margin:0 10px;padding:5px 15px;border:2px solid white;border-radius:5px;background-color:#404040;color:white;box-shadow:rgba(0,0,0,0.24) 0px 3px 8px}.main .timer button:hover{background-color:#595959}.main .timer .title{font-size:2.5rem;padding:1rem}.main .timer .display-and-control-timer{border-radius:10px;padding:2rem 0rem;margin:1rem;border:4px solid white;background-color:#404040;box-shadow:rgba(0,0,0,0.25) 0px 25px 50px -12px}.main .timer .display-and-control-timer #timer-label{font-size:1.5rem;margin-bottom:0}.main .timer .display-and-control-timer #time-left{font-weight:bold;color:#E34E4E;font-size:2.5rem;letter-spacing:10px;padding:1rem}.main .timer .display-and-control-timer .control-timer-btn{color:white;background-color:#167c7e;padding:5px 20px;margin:0 5px}.main .timer .display-and-control-timer .control-timer-btn:hover{background-color:white;color:#167c7e}.main .timer .config-timer{padding:1rem}.main .timer .config-timer .col{text-align:center}.main .timer .config-timer label{font-size:1.5rem}.main .timer .author{margin-top:3rem;letter-spacing:1px}

